@import url('https://fonts.googleapis.com/css2?family=Nova+Round&display=swap');

* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  cursor: url(./images/cursor.png), auto;
}

body {
  font-family: 'Nova Round', cursive;
  background-color: rgb(25, 23, 18);
  color: aliceblue; 
  overflow-x: hidden;
}

.link {
  text-decoration: none;
  color: rgba(203, 179, 106);
}

.fadeIn {
  animation: 1s ease-out fadeIn 1;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.fadeInDown {
  animation: 1s ease-out fadeInDown 1;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -2000px, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.fadeInLeft {
  animation: 1s ease-out fadeInLeft 1;
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

.fadeInRight {
  animation: 1s ease-out fadeInRight 1;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.fadeInUp {
  animation: 1s ease-out fadeInUp 1;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}


.upDown {
  animation-name: upDown;
  animation-iteration-count: infinite;
  animation-duration: 4s;
}

@keyframes upDown {
  0% {
    top: 0px;
  }

  50% {
    top: 200px;
  }

  100% {
    top: 0px;
  }
}


body::-webkit-scrollbar {
  width: 5px;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px black;
}

body::-webkit-scrollbar-thumb {
  /* background-color: #229b93; */
  background-image: linear-gradient(to top, rgba(203, 179, 106, 0.518), azure);
  /* background-image: linear-gradient(to top, rgb(0, 0, 0), rgb(160, 186, 12)); */
  outline: 0px solid #229b93;
}